import * as React from "react"
import Layout from "../components/Layout"
import WaveHeader from "../components/helpers/WaveHeader"
import GoogleForm from "../components/helpers/Form" 
import { useStaticQuery, graphql } from "gatsby" 

const Html = () => {
  const data = useStaticQuery(graphql`
  query pedidos{ 
      site {  
        siteMetadata{ 
            forms {   
              sugerencias 
            }
          } 
      }
    }
  `) 
   console.log(data);
  return (
    <Layout> 
     <WaveHeader fill="#00000"/>
     saddasd
     <GoogleForm src={data.site.siteMetadata.forms.sugerencias}/>
    </Layout>
  )
}

export default Html
 